@import url('https://fonts.googleapis.com/css2?family=Staatliches&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap');


.container.first{
    width: 100%;
    background-color: #FAF4B7;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
  
  
    
}
.container.first .content{
    width: 60%;
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #7D9D9C;
    
    font-weight: 600;
    /* -webkit-text-stroke: 1px #FFAC41;  */
}


.content .head{
    width: 100%;
    display: flex;
    color: #354259;
    position: relative;
    /* font-family: 'Staatliches', cursive; */
    font-family: 'Libre Baskerville', serif;
    font-size: 80pt; 
    justify-content: flex-start;
    

}




.content .writeup{
    width: 100%;
    position: relative;
    display: flex;
    font-family: 'Libre Baskerville', serif;
    font-size: 25pt; 
    justify-content: flex-start;;
}

.container.first .image{
    width: 40%;
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    /* position: relative; */
    /* background-color: aliceblue; */
}

.container.first .image .day {
    position: absolute;
    top: 80px;
    left: -10px;
    visibility: visible;
}


.container.first .image .day img{
    height: 500px;
}

.container.first .image .night{
    position: absolute;
    top: 80px;
    left: -10px;
    visibility: visible;
}


.container.first .image .night img{
    height: 500px;
}


